.searchBar form{
    width: 100%;
    display: flex;
    align-self: center;
    padding: 0 5rem;
    overflow: hidden;
    margin: 2rem 0;
}

.searchBar input{
    display: block;
    width: 80%;
    margin: 0;
    padding: 0.75rem;
    border: 1px solid #cccccc;
    font-size: 1.05rem;
    border-radius: 5px 0 0 5px;
}

.searchBar button{
    display: block;
    width: 20%;
    margin: 0;
    border: none;
    font-size: 1.05rem;
    border-radius: 0 5px 5px 0;
    font-weight: bold;
    background-color: #28abb9;
    color:#effad3;
    cursor: pointer;
    transition: 0.2s all ease-in-out;
}

.searchBar button:hover{
    background-color: #1f7c86;
}

@media only screen and (max-width: 700px){
    .searchBar form{
        padding: 0 1rem;
    }
}

@media only screen and (max-width: 500px){
    .searchBar form{
        padding: 0;
    }

    .searchBar input{
        width: 75%;
    }

    .searchBar button{
        width: 25%;
    }
}